import React, { useEffect, useState } from 'react'


const DefaultEdit = (props) => {
    
    let [name, setName] = useState("");
    let [email, setEmail] = useState("");
    let [role, setRole] = useState("");
    
    useEffect(() => {
        console.log("Component Did Mount", process.env.NODE_ENV,"props");
      // eslint-disable-next-line react-hooks/exhaustive-deps
        setName(props.editData.name);
        setEmail(props.editData.email);
        setRole(props.editData.role);
    }, []);

    
    return (
        <>
            <div className="row mt-5">
                <div className="col-md-3 text-center">
                    <button 
                        type="button" 
                        class="btn btn-info"
                        onClick={()=>{
                            props.onGoBack();
                        }}
                    >
                            {"<"} Back
                    </button>
                </div>
                <div className="col-md-6">
                    <form>
                        <label for="userName" class="form-label">Name</label>
                        <input 
                            type="text" 
                            className="form-control" 
                            id="name" 
                            aria-describedby="emailHelp"
                            value={name}
                            onChange={(event)=>{
                                setName(event.target.value);
                            }}
                        />
                        <label for="exampleInputEmail1" class="form-label mt-3">Email address</label>
                        <input 
                            type="email" 
                            className="form-control" 
                            id="Email" 
                            aria-describedby="emailHelp"
                            value={email}
                            onChange={(event)=>{
                                setEmail(event.target.value);
                            }}
                        />
                        <label for="exampleInputEmail1" class="form-label mt-3">Role</label>
                        <select class="form-select" aria-label="Default select example" value={role} onChange={(event)=>{
                            setRole(event.target.value);
                        }}>
                            <option value="admin">Admin</option>
                            <option value="member">Member</option>
                        </select>
                    </form>
                </div>
                <div className="col-md-3 text-center">
                    <button 
                        type="button" 
                        class="btn btn-info mr-3"
                        onClick={()=>{
                            props.onSave({
                                id : props.editData.id,
                                name,
                                email,
                                role
                            });
                        }}
                    >Save</button>
                </div>
            </div>   
              
        </>
    )
}
export default DefaultEdit;